<template>
  <div class="container my-4">
    <h5 class="mb-4">2-1 画面への表示</h5>
    <!-- セクション1 -->
    <div class="card mb-4 bg-primary bg-opacity-10 border-primary">
      <div class="card-header fw-bold text-primary">
        ■ 基本の出力：print()で1行ずつ表示
      </div>
      <div class="card-body">
        <pre class="code-block">
print('こんにちは。')
print('はじめまして。')
        </pre>
        <p><strong>解説：</strong> print() は出力後に自動で改行されます。</p>
        <div class="output-block mt-2">
<pre>
こんにちは。
はじめまして。
</pre>
        </div>
      </div>
    </div>

    <!-- セクション2 -->
    <div class="card mb-4 bg-primary bg-opacity-10 border-primary">
      <div class="card-header fw-bold text-primary">
        ■ 改行せずに続けて表示：end="" の利用
      </div>
      <div class="card-body">
        <pre class="code-block">
print('こんにちは。', end='')
print('はじめまして。')
        </pre>
        <p><strong>解説：</strong> end='' を指定すると、改行されずに表示が続きます。</p>
        <div class="output-block mt-2">
<pre>
こんにちは。はじめまして。
</pre>
        </div>
      </div>
    </div>

    <!-- セクション3 -->
    <div class="card mb-4 bg-primary bg-opacity-10 border-primary">
      <div class="card-header fw-bold text-primary">
        ■ 文字列内での改行：\n を使う
      </div>
      <div class="card-body">
        <pre class="code-block">
print('風\n林\n火\n山')
        </pre>
        <p><strong>解説：</strong> \n は文字列中での明示的な改行です。</p>
        <div class="output-block mt-2">
<pre>
風
林
火
山
</pre>
        </div>
      </div>
    </div>

    <!-- セクション4 -->
    <div class="card mb-4 bg-primary bg-opacity-10 border-primary">
      <div class="card-header fw-bold text-primary">
        ■ 複数の print() を使って1行ずつ表示
      </div>
      <div class="card-body">
        <pre class="code-block">
print('風')
print('林')
print('火')
print('山')
        </pre>
        <p><strong>解説：</strong> 各 print() は個別に改行されて出力されます。</p>
        <div class="output-block mt-2">
<pre>
風
林
火
山
</pre>
        </div>
      </div>
    </div>

    <!-- セクション5 -->
    <div class="card mb-4 bg-primary bg-opacity-10 border-primary">
      <div class="card-header fw-bold text-primary">
        ■ 改行だけを出力：空の print()
      </div>
      <div class="card-body">
        <pre class="code-block">
print()
print('はじめまして。')
        </pre>
        <p><strong>解説：</strong> 空の print() は1行の空白行を出力します。</p>
        <div class="output-block mt-2">
<pre>&nbsp;
はじめまして。
</pre>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
.code-block {
  background-color: #212529; /* 黒 */
  color: #f8f9fa;
  border: 1px solid #333;
  padding: 1rem;
  font-family: 'Courier New', Consolas, monospace;
  font-size: 0.95rem;
  white-space: pre;
  overflow-x: auto;
}

.output-block {
  background-color: #f8f9fa; /* 薄いグレー */
  border: 1px dashed #999;
  padding: 0.75rem 1rem;
  font-family: 'Courier New', Consolas, monospace;
  font-size: 0.95rem;
  white-space: pre-wrap;
}
</style>
